<template>
  <div class="user-public-nfts">
    <div class="section-header">
      <h2>{{ userTag === store.getters.userTag ? 'Your' : userTag }} NFTs Collection</h2>
    </div>
    
    <div class="tab-container">
      <div class="tabs">
        <div class="tabs-navigation">
          <button 
            class="tab-button" 
            :class="{ active: activeTab === 'owned' }"
            @click="setCurrentTab('owned')"
          >
            <i class="fas fa-wallet tab-icon"></i>
            Owned
            <div v-if="activeTab === 'owned'" class="active-indicator"></div>
          </button>
          <button 
            class="tab-button"
            :class="{ active: activeTab === 'rented' }"
            @click="setCurrentTab('rented')"
          >
            <i class="fas fa-box-open tab-icon"></i>
            Rented
            <div v-if="activeTab === 'rented'" class="active-indicator"></div>
          </button>
          <button 
            class="tab-button"
            :class="{ active: activeTab === 'created' }"
            @click="setCurrentTab('created')"
          >
            <i class="fas fa-paint-brush tab-icon"></i>
            Created
            <div v-if="activeTab === 'created'" class="active-indicator"></div>
          </button>
        </div>
      </div>

      <div class="tab-content">
        <div v-if="loading" class="loading-state">
          <div class="spinner"></div>
          <p>Loading NFTs...</p>
        </div>
        
        <p v-else-if="error" class="error-message">{{ error }}</p>
        
        <p v-else-if="currentNfts.length === 0" class="empty-message">
          <span class="empty-message-text">
            {{ userTag === store.getters.userTag ? 'You' : 'This user' }} 
            {{ emptyStateMessage }}
          </span>
          <router-link v-if="activeTab === 'owned'" to="/nft-mint" class="empty-message-button">Mint New NFT</router-link>
          <router-link v-if="activeTab === 'rented'" to="/nft-rental" class="empty-message-button">Rent NFT</router-link>
          <span v-if="activeTab === 'created'" class="empty-message-text">Soon every BITCAT NFT holder will be able to create their own NFTs and trade them on a decentralized auction and more. We are working on it!</span>
        </p>
        
        <div v-else class="nfts-grid">
          <div v-for="(nft, key) in currentNfts" :key="key + nft.assetPublicKey" class="nft-card">
            <div class="nft-image-container">
              <img :src="nft.imageUri" :alt="nft.name" class="nft-image">
            </div>
            <div class="nft-info">
              <h3>{{ nft.name }}</h3>
              <p class="nft-date">{{ getNftStatusText(nft) }} {{ formatDate(nft.createdAt) }}{{ nft.rentInfo ? ` for ${nft.rentInfo.days}d` : '' }}</p>
              <div class="nft-actions">
                <router-link :to="`/nft/${nft.assetPublicKey}`" class="view-button">
                  View Details
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Pagination -->
    <div v-if="currentPagination.totalPages > 1" class="pagination">
      <button 
        @click="changePage(1)" 
        :disabled="currentPagination.page === 1"
        class="pagination-button"
      >
        &laquo;
      </button>
      
      <button 
        @click="changePage(currentPagination.page - 1)" 
        :disabled="currentPagination.page === 1"
        class="pagination-button"
      >
        &lsaquo;
      </button>
      
      <div class="pagination-info">
        Page {{ currentPagination.page }} of {{ currentPagination.totalPages }}
      </div>
      
      <button 
        @click="changePage(currentPagination.page + 1)" 
        :disabled="currentPagination.page === currentPagination.totalPages"
        class="pagination-button"
      >
        &rsaquo;
      </button>
      
      <button 
        @click="changePage(currentPagination.totalPages)" 
        :disabled="currentPagination.page === currentPagination.totalPages"
        class="pagination-button"
      >
        &raquo;
      </button>
    </div>
    

  </div>
</template>

<script>
import axios from 'axios';
import { ref, onMounted, watch, computed } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import store from '@/vuex';

export default {
  name: 'UserPublicNftsList',
  props: {
    userTag: {
      type: String,
      required: true
    },
    nftsData: {
      type: Object,
      default: null
    },
    loading: {
      type: Boolean,
      default: false
    },
    error: {
      type: String,
      default: null
    },
    activeTab: {
      type: String,
      default: 'owned'
    }
  },
  
  setup(props, { emit }) {
    const route = useRoute();
    const router = useRouter();
    
    const currentNfts = computed(() => {
      if (!props.nftsData) return [];
      
      switch (props.activeTab) {
        case 'owned':
          return props.nftsData.owned.nfts;
        case 'rented':
          return props.nftsData.rented.nfts;
        case 'created':
          return props.nftsData.created.nfts;
        default:
          return [];
      }
    });
    
    const currentPagination = computed(() => {
      if (!props.nftsData) return { total: 0, page: 1, limit: 10, totalPages: 0 };
      
      switch (props.activeTab) {
        case 'owned':
          return props.nftsData.owned.pagination;
        case 'rented':
          return props.nftsData.rented.pagination;
        case 'created':
          return props.nftsData.created.pagination;
        default:
          return { total: 0, page: 1, limit: 10, totalPages: 0 };
      }
    });
    
    const emptyStateMessage = computed(() => {
      switch (props.activeTab) {
        case 'owned':
          return "hasn't minted any NFTs yet.";
        case 'rented':
          return "hasn't rented any NFTs yet.";
        case 'created':
          return "hasn't created any NFTs yet.";
        default:
          return "has no NFTs in this category.";
      }
    });
    
    // Функция для определения текста статуса NFT в зависимости от таба
    const getNftStatusText = (nft) => {
      switch (props.activeTab) {
        case 'owned':
          return 'Minted on';
        case 'rented':
          return 'Rented on';
        case 'created':
          return 'Created on';
        default:
          return 'Added on';
      }
    };
    
    // Функция для установки текущего таба
    const setCurrentTab = (tab) => {
      emit('changeTab', tab);
    };
    
    // Функция fetchUserNfts была перенесена в родительский компонент
    
    const changePage = (newPage) => {
      // Проверяем, что новая страница в пределах допустимых значений
      if (newPage < 1 || newPage > currentPagination.value.totalPages) {
        return;
      }
      
      // Обновляем URL с новыми параметрами страницы и таба
      router.push({
        path: route.path,
        query: { ...route.query, page: newPage, tab: props.activeTab }
      });
      
      // Прокручиваем страницу вверх
      window.scrollTo({ top: 0, behavior: 'smooth' });
    };
    
    const formatDate = (dateString) => {
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      });
    };
    
    onMounted(() => {
      // Get tab from URL query parameter
      const tab = route.query.tab || 'owned';
      
      // Устанавливаем начальный таб из URL
      if (tab !== props.activeTab) {
        setCurrentTab(tab);
      }
    });
    
    return {
      currentNfts,
      currentPagination,
      emptyStateMessage,
      setCurrentTab,
      changePage,
      formatDate,
      store,
      getNftStatusText
    };
  }
};
</script>

