<template>
  <div class="player-nfts-page">
    <div class="container">
      <div v-if="loading" class="loading-state">
        <div class="spinner"></div>
        <p>Loading user information...</p>
      </div>
      
      <div v-else-if="error" class="error-state">
        <p>{{ error }}</p>
        <router-link to="/" class="back-button">Back to Home</router-link>
      </div>
      
      <template v-else>
        <div class="user-profile">
          <div class="user-avatar">
            <span class="avatar-placeholder">{{ userInitial }}</span>
          </div>
          <div class="user-info">
            <h1><router-link :to="`/player/${userTag}`">{{ userTag }}</router-link></h1>
            <div class="user-stats">
              <div class="stat-item" @click="setActiveTab('owned')" :class="{ active: activeTab === 'owned' }">
                <span class="stat-value">{{ nftsData?.owned?.pagination?.total || 0 }}</span>
                <span class="stat-label">Owned</span>
              </div>
              <div class="stat-item" @click="setActiveTab('rented')" :class="{ active: activeTab === 'rented' }">
                <span class="stat-value">{{ nftsData?.rented?.pagination?.total || 0 }}</span>
                <span class="stat-label">Rented</span>
              </div>
              <div class="stat-item" @click="setActiveTab('created')" :class="{ active: activeTab === 'created' }">
                <span class="stat-value">{{ nftsData?.created?.pagination?.total || 0 }}</span>
                <span class="stat-label">Created</span>
              </div>
            </div>
          </div>
          <div class="user-actions">
            <router-link to="/nft-mint" class="action-button mint-button">Mint New NFT</router-link>
            <router-link to="/nft-rental" class="action-button rent-button">Rent {{ isCurrentUser ? 'Out' : 'In' }}</router-link>
          </div>
        </div>
        
        <UserPublicNftsList 
          :userTag="userTag" 
          :nftsData="nftsData" 
          :loading="nftsLoading" 
          :error="nftsError" 
          :activeTab="activeTab"
          @changeTab="setActiveTab"
        />
      </template>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, watch } from 'vue';
import { useRoute } from 'vue-router';
import axios from 'axios';
import store from '@/vuex';
import UserPublicNftsList from '@/components/pages/nft-bitcat/UserPublicNftsList.vue';

export default {
  name: 'PlayerNftsPage',
  components: {
    UserPublicNftsList
  },
  
  setup() {
    const route = useRoute();
    const userTag = ref('');
    const loading = ref(true);
    const error = ref(null);
    
    // Данные NFT
    const nftsData = ref(null);
    const nftsLoading = ref(true);
    const nftsError = ref(null);
    const activeTab = ref('owned');
    
    const userInitial = computed(() => {
      if (userTag.value) {
        return userTag.value.charAt(0).toUpperCase();
      }
      return '';
    });
    
    const isCurrentUser = computed(() => {
      return store.getters.isAuthenticated && userTag.value === store.getters.userTag;
    });
    
    const totalNfts = computed(() => {
      if (!nftsData.value) return 0;
      
      const ownedTotal = nftsData.value.owned?.pagination?.total || 0;
      const rentedTotal = nftsData.value.rented?.pagination?.total || 0;
      const createdTotal = nftsData.value.created?.pagination?.total || 0;
      
      return ownedTotal + rentedTotal + createdTotal;
    });
    
    // Установка активного таба
    const setActiveTab = (tab) => {
      activeTab.value = tab;
    };
    
    // Загрузка данных NFT
    const fetchNftsData = async (page = 1) => {
      nftsLoading.value = true;
      nftsError.value = null;
      
      try {
        const response = await axios.get(`/nft-bitcat/user/${userTag.value}/nfts`, {
          params: { page, limit: 10, tab: activeTab.value }
        });
        
        if (response.data.success) {
          nftsData.value = response.data;
        } else {
          nftsError.value = 'Failed to load NFTs';
        }
      } catch (err) {
        console.error('Error fetching NFTs:', err);
        nftsError.value = err.response?.data?.message || 'An error occurred while fetching NFTs';
      } finally {
        nftsLoading.value = false;
      }
    };
    
    const fetchUserInfo = async () => {
      loading.value = true;
      error.value = null;
      
      try {
        // Get userTag from route params
        userTag.value = route.params.userTag;
        
        // Загружаем данные NFT пользователя
        await fetchNftsData();
        
      } catch (err) {
        console.error('Error fetching user info:', err);
        error.value = err.response?.data?.message || 'User not found or an error occurred';
      } finally {
        loading.value = false;
      }
    };
    
    onMounted(() => {
      fetchUserInfo();
    });
    
    // Отслеживаем изменения активного таба
    watch(activeTab, () => {
      // Не делаем новый запрос, так как данные уже загружены для всех табов
    });
    
    return {
      userTag,
      loading,
      error,
      nftsData,
      nftsLoading,
      nftsError,
      activeTab,
      totalNfts,
      userInitial,
      isCurrentUser,
      setActiveTab
    };
  }
};
</script>

